import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import login from '../views/login.vue'
import search from '../views/search'
import food from '../views/food'
import food_shoop from '../views/food_shoop'
import food_search from '../views/food_search'
import food_dingdan from '../views/food_dingdan'
import food_me from '../views/food_me'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
 
  {
    path: '/search',
    name: 'search',
    component: search
  },
  {
    path: '/food',
    name: 'food',
    component: food,
    redirect:"/food/food_shoop",
    children:[

        {
          path: 'food_search',
          name: 'food_search',
          component: food_search
        },
        {
          path: 'food_shoop',
          name: 'food_shoop',
          component: food_shoop
        },
        {
          path: 'food_dingdan',
          name: 'food_dingdan',
          component: food_dingdan
        },
        {
          path: 'food_me',
          name: 'food_me',
          component: food_me
        },
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
