<template>
    <div class="detail">
         
        <header>
            <p @click="home">{{ zuo }}</p>
            <p>{{$route.query.name}}</p>
        </header>

        <div class="search">

           <input v-model="keysword" type="text" placeholder="请输入地址">
           <p v-show="delshow" @click="clear">x</p>
           <button @click="btn()">提交</button>
        </div>

        <div class="searchList">

          
            <ul v-show="!flag" class="searchHistory">
              <p class="p">搜索历史</p>
              <li v-for="(i,k) in searchList" :key="k">
                 <p>{{i.name}}</p>
                      <p>{{i.address}}</p>
              </li>
              <p class="alldel" @click="clearAll" v-show="clearHistory">清空所有</p>
                 
            </ul>


            <ul  v-show="flag" class="list">
              <li @click="tofood(i)" v-for="(i,k) in list" :key="k">
                      <p>{{i.name}}</p>
                      <p>{{i.address}}</p>
              </li>
            </ul>
        </div>


    </div>
</template>

<script>
export default {
    data() {
        return {
            zuo:"<",
            keysword:"",
            list:[],
            delshow:"",
            searchList:localStorage.getItem("searchList")?JSON.parse(localStorage.getItem("searchList")):[],
            flag:false,
            clearHistory:false

        }
    },
    watch: {

      keysword(val){
          
          if(val==""){
                  this.delshow=false
                  this.flag=false
          } else{
                  this.delshow=true

          }

      },

      searchList:{

         handler(val){

         
          
          if(val.length>0){
                  this.clearHistory=true
          } else{
                   this.clearHistory=false

          }

          localStorage.setItem("searchList",JSON.stringify(val))
          

      },
      immediate:true

      }

    },
    mounted() {

    },
    methods: {
             clearAll(){

               this.searchList=[]

             },
             home(){
                 this.$router.push("/")
             },

             btn(){
                  this.$http.get(`/v1/pois?type=search&city_id=${this.$route.query.id}&keyword=${this.keysword}`).then((data)=>{
                    console.log(data.message)
                    this.list=data
                    this.flag=true

                  })

             },
             clear(){
               this.keysword=""
             },

             tofood(i){
                this.searchList.unshift(i)
               if(this.searchList.length>5){
                    
                    this.searchList.pop()
               }
                
               
               
              this.keysword=""
               this.flag=false
               console.log(i)
              this.$router.push({
                path:"/food",
                query:{
                   geohash:i.geohash,
                   name:i.name
                }
              })
               
             }
            
    }
};
</script>

<style lang="scss" scoped>
.searchList{
  width: 100%;
  padding-top: 20px;
  
  >p{
    width: 100%;
    height: 30px;
    margin-bottom: 10px;
    line-height: 30px;
   text-indent: 20px;
    border-bottom:1px solid seagreen ;
  }
  >ul{
    width: 100%;
    display: flex;
    flex-direction: column;
    .p{
      width: 100%;
      height: 30px;
      line-height: 30px;
      text-indent: 20px;
      font-weight: bold;
    }
    >.alldel{
      width: 100%;
      height: 60px;
      text-align: center;
      line-height: 60px;
      font-size: 20px;
      font-weight: bold;
    }
    li{
      width: 100%;
      
      line-height: 40p;
      text-indent: 20px;
      border-bottom: 1px solid #ccc;
      p{
        height: 30px;
        line-height: 30px;
        overflow: hidden;    
        text-overflow:ellipsis;    
        white-space: nowrap;
      }
    }
  }
}
.search{
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
  input{
    width: 70%;
    height: 40px;
    font-size: 20px;
    z-index: 10px;
  }
  p{
    color: red;
    font-size: 20px;
    position: absolute;
    font-weight: bold;
    left: 369px;
    top: 26px;
  }
  button{
    width: 70%;
    height: 40px;
    text-align: center;
    line-height: 42px;
  background: #3190e8;
   color: white;
   font-size: 20px;
  }
}
.detail {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f5f5f5;
}
header {
  width: 100%;
  height: 60px;
  background: #3190e8;
  display: flex;
  align-items: center;
  > :nth-child(1) {
    font-size: 40px;
    color: white;
    margin-left: 50px;
    margin-right: 22%;
  }
  > :nth-child(2) {
    font-weight: bold;
    font-size: 28px;
    color: white;
  }
}

</style>
